import '../../App.css';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import CssBaseline from '@material-ui/core/CssBaseline';
import SectionBlock from "../../components/SectionBlock";
import { makeStyles } from '@material-ui/core/styles';
import People_icon from '../../People_icon.png';
import Wallet_icon from '../../Wallet_icon.png';
import Rectangle_icon from '../../Rectangle_icon.png';
import Calendar_icon from '../../Calendar_icon.png';
import Avatar_Ivanov from '../../Avatar_Ivanov.png';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import SettingsIcon from '@material-ui/icons/Settings';
import NotificationsIcon from '@material-ui/icons/Notifications';
import {IconButton} from "@material-ui/core";
import {Link} from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: "100%",
        backgroundColor: 'rgb(255,255,255)',
    },

    input: {
        fontSize: "12px",
        color: 'rgb(22,111,255)',
        textTransform: 'uppercase',
        fontWeight: 'bold',
        padding: 20,
    },

    hr: {
        margin: "20px auto",
        borderTop: "1px solid rgba(0, 0, 0, 0.1)",
        borderBottom: "1px solid rgba(255, 255, 255, 0.3)",
        border: 0,
        height: 0,
        maxWidth: "45%",
        [theme.breakpoints.down('600')]: {
            maxWidth: "90%",
        },
    },

    UpperBlock: {
        background: 'rgb(22,111,255)',
        padding: 15,
        borderBottomRightRadius: 50,
    },

    name: {
        fontSize: 36,
        fontWeight: "bold",
        color: "white",
        textAlign: "center",
    },

    email: {
        fontSize: 14,
        color: 'rgb(159,191,244)',
    },
}));

function App() {
    const classes = useStyles();
    return (
        <div className="App">
            <header className="App-header">
                <React.Fragment>
                    <div className={classes.root}>
                        <Grid container direction="column" className={classes.UpperBlock}>
                            <Grid container justifyContent="space-between">
                                <Grid item>
                                    <IconButton aria-label="delete">
                                        <SettingsIcon fontSize="large" style={{ color: 'rgb(159,191,244)' }}/>
                                    </IconButton>
                                </Grid>
                                <Grid item>
                                    <IconButton aria-label="delete">
                                        <NotificationsIcon fontSize="large" style={{ color: 'rgb(159,191,244)' }}/>
                                    </IconButton>
                                </Grid>
                            </Grid>
                            <Grid item><img src={Avatar_Ivanov}/></Grid>
                            <Grid item><div className={classes.name}>Иван Иванов</div></Grid>
                            <Grid item><div className={classes.email}>ivanov.i.o@mstroy.pro</div></Grid>
                        </Grid>
                        <hr className={classes.hr}/>
                        <Grid container justifyContent="center" >
                            <SectionBlock icon={People_icon} title={'Бригада'} subtitle={'Всего человек на площадке:'} value={45} />
                            <SectionBlock icon={Wallet_icon} title={'Выплаты'} subtitle={'Всего к выплате (₽):'} value={250745}/>
                            <SectionBlock icon={Rectangle_icon} title={'Задачи'} subtitle={'Всего задач в работе:'} value={8}/>
                            <SectionBlock icon={Calendar_icon} title={'Сроки'} subtitle={'Осталось дней:'} value={14}/>
                        </Grid>
                        <hr className={classes.hr}/>
                        <Grid container alignItems="center" justifyContent="center">
                            <Grid item>
                                <div className={classes.input}>Внести объем выполненных работ</div>
                            </Grid>
                            <Grid item>
                                <Link to="/VolumeWorkPage">
                                    <IconButton aria-label="delete">
                                        <AddCircleIcon fontSize="large" style={{ color: 'rgb(22,111,255)' }}/>
                                    </IconButton>
                                </Link>
                            </Grid>
                        </Grid>
                        <hr className={classes.hr}/>
                    </div>
                </React.Fragment>
                <CssBaseline />
            </header>
        </div>
    );
}

export default App;
