import React from 'react';
import Grid from "@material-ui/core/Grid";
import {makeStyles} from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({

    root: {
        flexGrow: 1,
    },

    block_value: {
        background: 'rgb(47,128,237)',
        borderRadius: "10px",
        padding: theme.spacing(1),
        display: "flex",
        justifyContent: "center",
    },

    value : {
        fontSize: "18px",
        fontWeight: 'bold',
        color: 'rgb(255,255,255)',
    },

    title: {
        fontSize: 14,
        color: 'rgb(22,111,255)',
        textTransform: 'uppercase',
        fontWeight: 'bold',
        verticalAlign: "middle",
        textAlign: "left",
    },

    block: {
        padding: 5,
    },

}));

function SectionBlockMini({ title, value }) {
    const classes = useStyles();
    return (
        <Grid className={classes.block} container justifyContent="center" alignItems="center">
            <Grid item xs={7} sm={4} md={3} lg={2}><div className={classes.title}>{title}:</div></Grid>
            <Grid item xs={3} sm={2} md={2} lg={1} className={classes.block_value}><div className={classes.value}>{value}</div></Grid>
        </Grid>
    );
}

export default SectionBlockMini;